/* Header Responsive Styles */

/* AppBar Styles */
.header-appbar {
  top: 0;
}

.header-appbar .MuiAppBar-root {
  background-color: #fff;
  box-shadow: 0;
  padding-left: 0;
  padding-right: 0;
}

/* Container Styles */
.header-container {
  max-width: 1440px !important;
}

/* Toolbar Styles */
.header-toolbar {
  justify-content: space-between;
  align-items: center;
}

/* Logo Styles */
.header-logo {
  width: 150px;
  margin-right: 8px;
  cursor: pointer;
}

/* Navigation Menu Styles */
.header-nav-menu {
  position: relative;
}

.header-nav-menu-desktop {
  display: flex;
}

.header-nav-menu-mobile {
  display: none;
}

.header-page-title-desktop {
  display: block;
}

.header-page-title-mobile {
  display: none;
}

/* Search Styles */
.header-search-desktop {
  display: flex;
  align-items: center;
}

.header-search-mobile {
  display: none;
}

/* Profile Icon Styles */
.header-profile-icon-mobile {
  display: flex;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  border: 2px solid #E6E6E6;
}

.header-profile-icon-mobile:hover {
  background-color: #f5f5f5;
}

.header-profile-icon-desktop {
  display: none;
}

/* Sign In Button Styles */
.header-signin-desktop {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  padding-left: 10px;
  padding-right: 10px;
}

.header-signin-mobile {
  display: none;
}

/* Mobile Menu Button */
.header-mobile-menu {
  flex-grow: 1;
  display: none;
}

/* Mobile Menu Content */
.header-mobile-menu-content {
  display: block;
}

.header-mobile-menu-content-desktop {
  display: none;
}

.header-mobile-search {
  display: flex;
  margin-bottom: 16px;
}

.header-mobile-search-desktop {
  display: none;
}

.header-mobile-signin {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
}

.header-mobile-signin-desktop {
  display: none;
}

/* Media Queries */

/* Mobile devices (up to 767px) */
@media (max-width: 767px) {
  .header-appbar .MuiAppBar-root {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  .header-nav-menu-desktop {
    display: none;
  }

  .header-nav-menu-mobile {
    display: flex;
  }

  .header-page-title-desktop {
    display: none;
  }

  .header-page-title-mobile {
    display: block;
  }

  .header-search-desktop {
    display: none;
  }

  .header-search-mobile {
    display: flex;
  }

  .header-profile-icon-mobile {
    display: flex;
  }

  .header-profile-icon-desktop {
    display: none;
  }

  .header-signin-desktop {
    display: none;
  }

  .header-signin-mobile {
    display: flex;
  }

  .header-mobile-menu {
    display: flex;
  }

  .header-mobile-menu-content {
    display: block;
  }

  .header-mobile-menu-content-desktop {
    display: none;
  }

  .header-mobile-search {
    display: flex;
  }

  .header-mobile-search-desktop {
    display: none;
  }

  .header-mobile-signin {
    display: flex;
  }

  .header-mobile-signin-desktop {
    display: none;
  }
}

/* Tablet and Desktop (768px and above) */
@media (min-width: 768px) {
  .header-appbar .MuiAppBar-root {
    padding-left: 40px !important;
    padding-right: 40px !important;
  }

  .header-nav-menu-desktop {
    display: flex;
  }

  .header-nav-menu-mobile {
    display: none;
  }

  .header-page-title-desktop {
    display: block;
  }

  .header-page-title-mobile {
    display: none;
  }

  .header-search-desktop {
    display: flex;
  }

  .header-search-mobile {
    display: none;
  }

  .header-profile-icon-mobile {
    display: none;
  }

  .header-profile-icon-desktop {
    display: flex;
  }

  .header-signin-desktop {
    display: flex;
  }

  .header-signin-mobile {
    display: none;
  }

  .header-mobile-menu {
    display: none;
  }

  .header-mobile-menu-content {
    display: none;
  }

  .header-mobile-menu-content-desktop {
    display: block;
  }

  .header-mobile-search {
    display: none;
  }

  .header-mobile-search-desktop {
    display: block;
  }

  .header-mobile-signin {
    display: none;
  }

  .header-mobile-signin-desktop {
    display: flex;
  }
}

/* Large Desktop (1025px and above) */
@media (min-width: 1025px) {
  .header-mobile-signin-desktop {
    display: none;
  }
}









